<template>
  <div class="addCoupon">
    <div class="top">
      <div class="cancel" @click="hideDialog"></div>
    </div>
    <div class="title">添加优惠券</div>
    <div class="form">
      <div class="form-item name">
        <label for="coupon-name">优惠券名称</label>
        <input
          type="text"
          placeholder="请输入标题"
          id="coupon-name"
          class="form-item-content"
          v-model="monitoredMixedObj.name"
          ref="name"
          @keyup.enter="$refs.limit.focus()"
        />
      </div>
      <div class="form-item">
        <label for="type">优惠券类型</label>

        <select v-model="monitoredMixedObj.type" id="type">
          <option value="0" selected>满减类型</option>
        </select>
      </div>
      <div class="form-item">
        <label for="discount">优惠数额</label>
        <input
          type="text"
          id="discount"
          placeholder="请填写优惠数额"
          v-model="monitoredMixedObj.discount"
        />
      </div>
      <div class="form-item">
        <label for="limit">使用条件</label>
        <input
          type="text"
          id="limit"
          placeholder="请填写使用条件"
          v-model="monitoredMixedObj.limit"
        />
      </div>
    </div>
    <div class="buttonZone">
      <button class="cancelBtn other-button" @click="hideDialog">取消</button>
      <button class="ensureBtn submit-button" @click="checkInput">确认</button>
    </div>

    <div class="edit-reason" v-show="ifShowEditReason">
      <edit-reason-dialog
        @cancelClick="hideEditReason"
        @ensureClick="submit"
      ></edit-reason-dialog>
    </div>
  </div>
</template>

<script>
import uuid from "uuid";
import EditReasonDialog from "@/pages/internal/components/content/adminTools/editReasonDialog/EditReasonDialog";
import { $createCoupon } from "@/pages/internal/network/adminTools/coupon";
export default {
  name: "e-AddCoupon",
  data() {
    return {
      monitoredMixedObj: {
        name: "",
        limit: "",
        discount: "",
        type: "0",
      },
      ifShowEditReason: false,
    };
  },
  methods: {
    hideDialog() {
      this.$emit("hideDialog");
    },
    checkInput() {
      if (this.monitoredMixedObj.name === "") {
        this.$toast.error("请填写优惠券名称", 240);
        return;
      }
      if (this.monitoredMixedObj.limit === "") {
        this.$toast.error("请填写使用条件", 240);
        return;
      }
      if (this.monitoredMixedObj.discount === "") {
        this.$toast.error("请填写优惠数额", 240);
        return;
      }
      this.showEditReason();
    },
    showEditReason() {
      this.ifShowEditReason = true;
    },
    hideEditReason() {
      this.ifShowEditReason = false;
    },
    async submit(edit_reason) {
      let data = {
        name: this.monitoredMixedObj.name,
        type: this.monitoredMixedObj.type, //在那时不变
        discount: parseInt(Number(this.monitoredMixedObj.discount) * 100), //‘分’为计数单位
        limit: parseInt(Number(this.monitoredMixedObj.limit) * 100), //‘分’为计数单位
        create_reason: edit_reason,
      };
      try {
        const response = await $createCoupon(data);
        const { Code, msg } = response;
        if (Code === 200 && msg === "success") {
          this.$toast.success("提交成功", 240);
        } else {
          this.$toast.error("提交失败", 240);
        }
      } catch (e) {
        this.$toast.error("提交失败", 240);
      }
      this.hideEditReason();
    },
  },
  watch: {},
  components: {
    EditReasonDialog,
  },
};
</script>

